
// colors

$c_bg: #21252B;
$c_green: #6fcaa0;
$c_lightgreen: #9ee2c3;
$c_gray: #282a36;
$c_gold: #ddc239;
$c_red: #ea277c;
$c_orange: #ef941c;
$c_blue: #4c6aef;
$c_purple: #b333dd;
$c_dark: #171921;
$c_txt: darken(#d9dae1, 5%);
$c_ltgray: #56576a;
$c_pink: lighten($c_red, 14%);
$rainbow: (
    $c_gold,
    $c_orange,
    $c_red,
    $c_purple,
    $c_blue
);

// imports

@import url('https://fonts.googleapis.com/css?family=Josefin+Sans|ZCOOL+KuaiLe');
@import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro|Varela');
@import url('https://fonts.googleapis.com/css?family=Josefin+Sans:300,400');

// mixins

@mixin background-color-alpha($color, $alpha) {
    background-color: rgba(red($color), green($color), blue($color), $alpha);
}

@mixin absolute-fill($marg) {
    top: $marg;
    left: $marg;
    right: $marg;
    bottom: $marg;
}

// keyframes

@keyframes roll-vertical {
    0%   { transform: translateY(0); }
    100% { transform: translateY(-50%); }
}

@keyframes xpand-out {
    0% {
        left: 50%;
        right: 50%;
    }
    100% {
        left: 0;
        right: 0;
    }
}

@keyframes xpand-in {
    100% {
        left: 50%;
        right: 50%;
    }
    0% {
        left: 0;
        right: 0;
    }
}

@keyframes fadein-blur {
    0%   { filter: blur(0px); }
    100% { filter: blur(3px); }
}

@keyframes fadein-bgcolor {
    0%   { background-color: rgba(0, 0, 0,  0); }
    100% { background-color: rgba(0, 0, 0, .5); }
}

@keyframes fadein-drawer-bottom {
    0%   { transform: translateY(100%); opacity: 0; }
    6%   { opacity: 0; }
    7%   { opacity: 1; }
    44%  { transform: translateY(100% + -104.63%); }
    59%  { transform: translateY(100% + -98.36%); }
    73%  { transform: translateY(100% + -100.58%); }
    88%  { transform: translateY(100% + -99.8%); }
    100% { transform: translateY(0%); }
}

// global tags
body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: $c_gray;
    color: $c_txt;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

a {
    text-decoration: none;
    color: $c_gold;

    &.link {
        position: relative;
        transition: color 0.2s;
        white-space: nowrap;

        &::after {
            content: '';
            position: absolute;
            bottom: -2px;
            left: 50%;
            right: 50%;
            border-bottom: 2px solid transparent;
            animation: xpand-in 0.16s ease-in;
            border-color: $c_green;
        }

        &:hover {
            cursor: pointer;

            &::after {
                left: 0;
                right: 0;
                bottom: -2px;
                animation: xpand-out 0.16s;
                transition: border-color 0s;
            }
        }
    }

    &:hover {
        color: $c_green;
    }
}

// global classes

.scroll-thumb {
    background-color: darken($c_gray, 1.2%);
    border-radius: 8px;
}

.blur {
    animation: fadein-blur 1s;
    filter: blur(3px);
}

.focus {
    animation: fadein-bgcolor 1s;
    position: fixed;
    z-index: 86;
    @include absolute-fill(0);
    background-color: rgba(0, 0, 0, .5);
    display: flex;
    justify-content: center;
    flex-direction: column;

    .video {
        max-width: 80%;
        margin: 0 auto;
        animation: fadein-drawer-bottom 1s;

        video {
            width: 100%;
            box-shadow: 0 1px 8px rgba(0, 0, 0, .6);
        }
    }
}

.rendered-markdown {
    max-width: 60rem;
    margin: 0 auto;
    padding: 2rem;
    font-size: 1.1rem;

    hr {
        color: $c_dark;
    }

    h1, h2, h3 {
        color: $c_green;
        font-family: serif;
    }

    strong {
        color: #D19A66;
        font-weight: bold;
    }

    em {
        color: $c_pink;
    }


    .dotted-circle {
        display: inline-block;
        border: 3px dotted $c_orange;
        border-radius: 1rem;
        width: 1.4rem;
        height: 1.4rem;
        transform: translateY(25%);
    }

    .video {
        width: 68%;
        margin: 2rem auto;
        position: relative;
        transform: scale(0.98);
        transition: 0.14s all;

        video {
            width: 100%;
            box-shadow: 0 1px 11px rgba(0, 0, 0, .31);
            border-radius: 6px;
            opacity: 0.8;
            transition: 0.12s all;
            cursor: zoom-in;
        }

        &::after {
            content: 'GIF';
            position: absolute;
            top: 12px;
            right: 14px;
            background-color: rgba(0, 0, 0, .2);
            padding: 1px 5px;
            border-radius: 5px;
            transition: 0.12s all;
        }

        &:hover {
            transform: scale(1);

            &::after {
                opacity: 0;
            }

            video {
                opacity: 1;
            }
        }
    }

    .code {
        background-color: $c_dark !important;
        border-radius: 6px;
        margin: 0;

        .hljs-keywords {
            color: #C678DD;
        }

        &.block {
            box-shadow: 0 1px 8px rgba(0, 0, 0, .3);
            //padding: .5rem .7rem !important;
            max-height: 32rem;
            position: relative;
            z-index: 2;
            margin: 1rem 0;
        }

        &.inline {
            display: inline !important;
            padding: 0.2rem 0.3rem !important;
            font-size: 0.9rem !important;
        }
    }
}

.section-title {
    padding-top: 1rem;
    padding-bottom: 2rem;
    position: relative;
    text-align: center;
    display: flex;
    color: $c_green;
    font-family: 'Josefin Sans', sans-serif;

    > div {
        width: 50%;
        flex-shrink: 1000;
        //border-top: 3px solid $c_green;
        margin-top: 0.453 * 2.4rem;
        height: 3px;
        background-color: $c_green;
        border-radius: 2px;
    }

    span {
        font-size: 2.4rem;
        flex-shrink: 0;
        padding: 0 1rem;
    }
}

#theme-dracula {
    background-color: #21222C;
    min-height: 100vh;

    .window {
        padding: 4rem 1rem 1rem;
        display: flex;
        justify-content: center;
    }
}
// pages
.page:not(#frontpage) {
    min-height: 100vh;
    width: 100%;
}
$xmarg: 4%;

#frontpage {
    display: flex;
    justify-content: center;
    flex-direction: column;
    overflow: hidden;
    position: relative;
    height: 92vh;
    width: 92%;
    max-height: 52.5vw;
    margin: 4vh $xmarg;
    font-family: 'Josefin Sans', sans-serif;
    $border: 0.24vw;

    &::before {
        position: absolute;
        content: '';
        right: 0;
        bottom: 0;
        left: 0;
        top: 0;
        border: $border solid $c_dark;
    }

    .attributes,
    .ribbons {
        list-style: none;
        display: flex;
        flex-direction: row;
        position: absolute;
        padding: 0;
        font-size: 1.2rem;
        @media(min-width:1400px) {
            font-size: 1.5rem;
        }
    }

    .ribbons {
        top: $border;
        left: $border;
        margin: 0;
        padding: 0;
        margin-left: 3vw;
        margin-top: -$border;
        $bg: transparent;

        li {
            $br: $c_dark;
            background-color: $bg;
            width: 3.2vw;
            height: 5vw;
            margin: 0 1rem;
            position: relative;
            border: $border solid $br;
            border-radius: 0.2rem;
            border-bottom: none;
            transition: 0.1s border-color;

            i {
                font-size: 1.6vw;
                width: 100%;
                text-align: center;
                margin-top: 2vw;
            }
            // &.l0 { color: $c_gold; }
            // &.l1 { color: $c_orange; }
            // &.l2 { color: $c_red; }
            @for $i from 0 through 4 {
                &.l#{$i} {
                    $c: nth($rainbow, $i + 1);
                    color: $c;

                    &:hover {
                        border-color: $c;

                        &::after {
                            border-color: $c;
                        }
                    }
                }
            }

            &::after {
                content: '';
                position: absolute;
                background-color: $bg;
                transition: 0.1s border-color;
                width: 100% * 0.709;
                height: 3.2vw * 0.709;
                bottom: -1.26vw;
                left: 0.37vw;
                transform: rotate(-45deg);
                border-bottom: $border solid $br;
                border-left: $border solid $br;
                box-shadow: -2px 2px 2px rgba(0,0,0,.18);
                border-radius: 0.1rem;
            }

            &:hover {
                cursor: pointer;
            }
        }
    }

    .attributes {
        bottom: 0;
        right: $border;
        bottom: $border;
        margin: 1rem 0.84rem;

        li {
            margin: 0 0.5rem;

            &::before {
                content: '#';
                color: $c_orange;
                padding-right: 4px;
                transition: color 0.2s;
            }

            &:hover::before {
                color: $c_green;
            }
        }
    }

    .lines {
        position: absolute;
        transform: rotate(-30deg);
        width: 141vw;
        height: 4vw;
        left: -27.1vw;

        .line {
            margin: 4vw * (1 / 5);
            width: 141%;
            border-top: 0.3vw solid;
        }

        .l0 {
            border-color: $c_gold;
        }

        .l1 {
            border-color: $c_orange;
        }

        .l2 {
            border-color: $c_red;
        }

        .l3 {
            border-color: $c_purple;
        }

        .l4 {
            border-color: $c_blue;
        }
    }

    .center {
        text-align: center;
        padding-bottom: 1rem;
    }

    .title {
        color: $c_green;
        background-color: $c_gray;
        // red: #e54966
        //font-family: 'ZCOOL KuaiLe', cursive;
        font-weight: 300;
        font-size: 6vw;
        margin-bottom: 0.8rem;
        padding: 2.4vw;
        position: relative;
    }
}

#about {
    padding: 0 $xmarg;
    margin-bottom: 4rem;

    .description {
        overflow: hidden;
        font-size: 1.2rem;
        padding: 0 19%;
        max-width: 52rem;
        margin: 0 auto;
        position: relative;
        color: $c_txt;
        filter: drop-shadow(0 1px 6px rgba(0, 0, 0, .5));

        .background {
            position: absolute;
            @include absolute-fill(14%);
            top: -90%;
            bottom: -90%;
            background-color: $c_dark;
            z-index: -2;
            transform: rotate(12deg);
            border: 4px solid $c_red;

            &::after,
            &::before {
                content: '';
                position: absolute;
                @include absolute-fill(-15px);
                border: 6px solid $c_orange;
                border-right-color: $c_purple;
                z-index: -3;
            }

            &::after {
                @include absolute-fill(-28px);
                border: 8px solid $c_gold;
                border-right-color: $c_blue;
            }
        }
    }
}

#webdev {
    display: flex;
    $gray: $c_ltgray;
    $dkgray: darken($gray, 6%);

    .left {
        width: 28%;
        flex-shrink: 0;
        margin: 1rem 0 1rem $xmarg;
        position: relative;
        z-index: 2;
        font-size: 0.8rem;
        overflow: hidden;

        &::after {
            position: absolute;
            content: '';
            left: 0;
            right: 0;
            top: 0;
            height: 2rem;
            background: linear-gradient(to top, rgba(red($c_gray), green($c_gray), blue($c_gray), 0) 0%, $c_gray 100%);
        }

        &::before {
            position: absolute;
            content: '';
            left: 0;
            z-index: 2;
            right: 0;
            bottom: 0;
            height: 2rem;
            background: linear-gradient(to bottom, rgba(red($c_gray), green($c_gray), blue($c_gray), 0) 0%, $c_gray 100%);
        }

        .codeWrapper {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
        }

        pre {
            padding-top: 0;
            animation: roll-vertical 80s linear infinite;
            overflow-x: hidden;

            &::after {
                position: absolute;
                content: '';
                right: 0;
                top: 0;
                bottom: 0;
                width: 2rem;
                background: linear-gradient(to right, rgba(red($c_gray), green($c_gray), blue($c_gray), 0) 0%, $c_gray 80%, $c_gray 100%);
            }
        }
    }

    .right {
        flex-shrink: 10;
        background-color: $gray;
        margin: 1rem $xmarg 1rem 0;
        box-shadow: 0 1px 6px #000000;
        border-radius: 0.1rem;
        overflow: hidden;

        .wrapper {
            border-radius: 0.2rem;
            $margin: 1rem;
            margin: $margin;
            min-height: calc(100% - #{$margin * 2});
            border: 2px solid $dkgray;
            max-width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
        }

        .title {
            padding: 2rem 0 0;
            margin: 0 2rem;
        }

        .description {
            margin: 0 auto;
            max-width: 42rem;
            text-align: center;
            background-color: $dkgray;
            font-size: 1.2rem;
            padding: 3px;
            border-radius: 0.1rem;
            border: 1rem solid $c_ltgray;

            .inner {
                padding: 1rem;
                border: 2px solid $gray;
                border-radius: 0.1rem;
            }
        }

        .showcase {
            $padx: 0.3rem;
            background-color: darken($gray, 6%);
            position: relative;
            padding: 3px 0;

            .slidebar {
                margin-top: 0.3rem;
                display: flex;
                flex-direction: row;
                justify-content: center;

                .button {
                    width: 0.5rem;
                    height: 0.5rem;
                    border: 0.2rem solid $gray;
                    background-color: $c_green;
                    margin: 0 0.1rem;
                    border-radius: 50%;
                    transition: 0.1s all;

                    &.selected {
                        border-color: $c_green;
                        background-color: $gray;
                    }

                    &:hover {
                        background-color: $c_green;
                        border-color: $c_green;
                        cursor: pointer;
                    }
                }
            }

            .gridview {
                display: flex;
                flex-wrap: wrap;
                padding: 0 1rem;
                position: relative;
                border-top: 2px solid $c_ltgray;
                border-bottom: 2px solid $c_ltgray;

                .website {
                    width: 30%;
                    margin: 1.5%;
                    position: relative;

                    img {
                        width: 100%;
                        transition: 0.2s all;
                        display: relative;
                        border-radius: 4px;
                        box-shadow: 0 1px 3px rgba(0,0,0,.4);

                        &::before {
                            content: '';
                            position: absolute;
                            background-color: $c_gray;
                            left: 0;
                            top: 0;
                            right: 0;
                            bottom: 0;
                        }
                    }

                    .name {
                        font-family: 'Josefin Sans', sans-serif;
                        text-align: center;
                        font-size: 1.2rem;
                        padding: 0.1rem 0.4rem;
                    }

                    .role {
                        opacity: 0;
                        color: #ffffff;
                        position: absolute;
                        top: 50%;
                        left: 1rem;
                        right: 1rem;
                        text-align: center;
                        transition: 0.2s all;
                    }

                    &:hover {
                        cursor: pointer;

                        img {
                            filter: brightness(60%) saturate(30%);
                            box-shadow: 0 0 0 3px #ddccff;
                        }

                        .role {
                            opacity: 1;
                        }
                    }
                }
            }
        }
    }
}
